<template>
  <section>
    <el-tabs class="el-col el-col-24" type="card" v-model="activeName" @tab-click="handelTabChange">
      <el-tab-pane label="门诊挂号" name="add">
        <section>
           <!-- 挂号 添加患者 -->
          <patient-info v-if="huanzheData" ref="huanzhe"></patient-info>
        </section>
      </el-tab-pane>
      <el-tab-pane label="未就诊" name="already">
      </el-tab-pane>
      <el-tab-pane label="已就诊" name="not">
      </el-tab-pane>
    </el-tabs>
    <!-- 挂号 患者列表 -->
    <el-row v-show="tableIsDisplay">
      <el-col :span="24">
        <patient-list ref="patientList" :op='false'></patient-list>
      </el-col>
    </el-row>
  </section>
</template>
<script>
import patient_info from '@/components/patient_info' // 患者信息
import patient_list from '@/components/patient_list' // 患者列表
export default {
    components:{
    "patient-info": patient_info,
    "patient-list": patient_list
  },
  data(){
    return{
      activeName: 'add', // 当前激活标签
      huanzheData: {}, // 患者数据
      tableData:[], // 表格数据
      tableIsDisplay: false, //表格显示
      tabIndex: 0, // 标签页当前序号
    }
  },
  methods:{
    handelTabChange(tab){ // 切换标签页
      let index=tab.index
      if(this.tabIndex == index){
        return
      }
      this.tabIndex = index
      if(index == 0){
        this.tableIsDisplay = false
      }else{
        this.tableIsDisplay = true
        let treatmentState = index==1 ? 0:1 // 切换 已就诊/未就诊 状态
        this.$refs.patientList.getData({
          treatmentState: treatmentState,
          index: 1
        })
      }
    }
  },
}
</script>
<style scoped>
.el-tabs .is-active{
  background: #000;
}
.el-tabs__item{
  font-size: 16px;
}

</style>



